@use '@mview/scss/utilities/functions' as *;
@use '@mview/scss/utilities/mixins' as *;
@use 'sass:color';

.file-info-wrapper {
  .file-info-content {
    display: grid;
    grid-template-columns: 160px 150px 150px 100px 80px;
    grid-template-rows: 1fr 1fr;
    justify-content: space-between;
    gap: 16px;
    row-gap: 32px;
    cursor: default;

    h6 {
      font-size: 14px;
      color: pick-color('primary', 'charcoal');
      line-height: 20px;
    }

    span {
      margin-top: 5px;
      font-size: 13px;
      color: pick-color('primary', 'ash');
    }

    h6,
    span {
      display: block;
      overflow: hidden;
      text-overflow: ellipsis;
      white-space: nowrap;
    }
  }
}
